@import url('https://fonts.googleapis.com/css?family=Core+Circus');

@mixin keyFrame($animeName) {
  @-webkit-keyframes #{$animeName} {
      @content;
    }
    @-moz-keyframes #{$animeName} {
      @content;
    }
    @keyframes #{$animeName} {
      @content;
    }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box
}

body1 {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #ea8b19;
  font-family: 'Raleway', sans-serif;
}
.box1 {
  position: absolute;
  width: 300px;
  z-index: 3;
  height: 300px;
  background-color: #F6E7D8;
  border-radius: 50%;
  top: 86%;
  left: 50%;
  margin-left: -150px; 
  overflow: hidden;
  &::before {
    content: '';
    position: absolute;
    inset: -10px 100px;
    background: linear-gradient(#000000, #000000);
    transition: .5s;
    animation: animate 4s linear infinite;
  }
  &::after {
    content: "";
    position: absolute;
    background-color: #ea8b19;
    inset: 10px;
    border-radius: 50%;
    z-index: 1;
  }
  .content {
    position: absolute;
    inset: 30px;
    border: 6px solid #F6E7D8;
    z-index: 3;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    flex-direction: column;
    img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      transition: .5s;
      text-align: center;
      pointer-events: none;
      z-index: 3;
    }
    h5 {
      position: relative;
      text-align: center;
      font-size: 15px;
      font-weight: 600;
      letter-spacing: 2px;
      color: #000000;
      padding: 10px 0;
      span {
        display: block;
        font-weight: 300;
        font-size: 13px;
        padding: 5px 0;
      }
    }
    a {
      position: relative;
      background-color: #fff;
      color: #000000;
      padding: 8px 10px;
      border-radius: 20px;
      font-size: 14px;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 1px;
      text-decoration: none;
      transition: .5s;
      &:hover {
        letter-spacing: 3px;
      }
    }
  }
  &:hover {
    &::before {
      inset: -20px 0px;
    }
    img {
      opacity: 0
    }
  }
}

@include keyFrame(animate) {
  0% {
    transform: rotate(0)
  }
  100% {
    transform: rotate(360deg)
  }
}